import React, { Component } from 'react';
import portal  from './Pictures/WebPortal.png';
import { Image } from 'react-bootstrap';


const pic = {
    width: "75%",
    height: "75%"
}

class Waitz extends Component {

    render() {
        return (
            <div className="main">
            <div className="container-fluid">
                <h3>
                    Waitz
                </h3>
                <div className="container-fluid">
                    <Image src={ portal } style={pic} thumbnail />
                </div>
                Using ReactJS and Amazon Web Services, developed an intuitive, scalable front end framework for present and future Waitz clients.
            </div>
            </div>

        );
    }

}


export default Waitz;


